<template>
  <div class="environment">
    <!-- 環境水體監測站專區 -->
    <div class="row" style="margin: 0 15px 0 15px">
      <div class="col-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp text-center title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >環境水體監測站</span
        >
      </div>
    </div>
    <!-- 河川污染指數介紹 -->
    <div class="childtitle one">你知道嗎?</div>
    <div class="subtitle">
      <span class="highlight">「環境水體監測站」</span>會監測河川的水質狀態，
      用於評估河川水質之綜合性指標為<span class="highlight"
        >「河川污染指數, River Pollution Index」</span
      >
      簡稱<span class="highlight">「RPI」</span>。<br />
      RPI指數係以<span class="highlight"
        >水中溶氧量（DO）、生化需氧量（BOD5）、懸浮固體（SS）、與氨氮(NH3-N）</span
      >等四項水質參數之濃度值，來計算所得之指數積分值，並判定河川水質污染程度。
    </div>
    <div class="content">
      <el-row :gutter="20">
        <el-col :xs="24" :span="16">
          <el-table
            :data="RPIExplanTable"
            style="width: 100%"
            :header-cell-style="tableHeaderColor"
            :row-style="{ background: '#FFFFF0' }"
          >
            <el-table-column prop="item" label="檢測項目"> </el-table-column>
            <el-table-column prop="nonePollution" label="未 ( 稍 ) 受汙染">
            </el-table-column>
            <el-table-column prop="littlePollution" label="輕度汙染">
            </el-table-column>
            <el-table-column prop="miiddlePollution" label="中度汙染">
            </el-table-column>
            <el-table-column prop="hardPollution" label="嚴重汙染">
            </el-table-column>
          </el-table>
        </el-col>
        <el-col :xs="24" :span="8">
          <el-card class="RPIexplain">
            <div>點數總計 / 水質項目數 = <b>" 污染指數積分值 "</b></div>
            <div>
              污染指數積分值 ≦ 2.0 :
              <span class="hightlight">未 ( 稍 ) 受汙染</span>
            </div>
            <div>
              2.0 ＜ 污染指數積分值 ≦ 3.0 :
              <span class="hightlight">輕度汙染</span>
            </div>
            <div>
              3.1 ≦ 污染指數積分值 ≦ 6.0 :
              <span class="hightlight">中度汙染</span>
            </div>
            <div>
              污染指數積分值 ＞ 6.0 : <span class="hightlight">嚴重汙染</span>
            </div>
            <div style="font-weight: bold; margin-top: 10px">
              ※ 點擊右方連結以了解詳細河川污染指數算法
              <a href="https://wq.epa.gov.tw/Code/Business/Standard.aspx"
                >河川污染指數詳細說明</a
              >
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <!-- 高雄主要河川目前最新污染資料 -->
    <hr class="hr" />
    <div class="childtitle two">高雄主要河川目前最新污染資料</div>

    <div v-loading="loading" element-loading-text="等待政府資料中...">
      <el-row class="newestRow" :gutter="20">
        <el-col :xs="24" :span="21">
          <chart
            :options="NewRiverChart"
            height="300"
            style="width: 100%; height: 300px"
          ></chart>
        </el-col>
        <el-col :xs="24" :span="3" class="newestTime">
          資料時間 : {{ nowYear }}
        </el-col>
      </el-row>
    </div>
    <!-- 高雄環境水體監測站位置 -->
    <hr class="hr" />
    <div class="childtitle three">高雄環境水體監測站</div>
    <div class="content">
      <div>
        <span class="touch1"> 下方為高雄所有環境監測站位置 </span><br />
        <span> <b>Tip：</b>點擊左方流域即可查看監測該流域水質的監測站 </span>
      </div>
      <el-row style="margin: 20px 0">
        <el-col :xs="24" :span="8"
          ><div>
            ※ 若欲查詢各測站的詳細監測資料，請至以下兩個網站查詢
          </div></el-col
        >
        <el-col :xs="24" :span="5"
          >1.
          <a href="https://ksenlab.ksepb.gov.tw/kh-taqm/zh-tw/WaterData.aspx"
            >高雄市政府環境保護局環境檢驗科</a
          ></el-col
        >
        <el-col :xs="24" :span="5"
          >2.
          <a
            href="https://wq.epa.gov.tw/Code/AdvSearch.aspx?YearSelect=3&StartYear=&EndYear=&Area=1660&PageID=5"
            >行政院環境保護署全國環境水質監測資訊網</a
          ></el-col
        >
      </el-row>
      <div v-loading="loading2" element-loading-text="等待政府資料中...">
        <!-- 高雄所有流域 -->
        <el-tabs
          type="border-card"
          stretch
          tab-position="top"
          v-model="searchRiver"
          @tab-click="searchStation"
        >
          <el-tab-pane
            v-for="item in RiverArray"
            :key="item.riverName"
            :label="item.riverName"
            :name="item.riverName"
          ></el-tab-pane>
        </el-tabs>
        <!-- 環境水體監測站地圖 -->
        <el-card class="box-card">
          <l-map class="map" :zoom="zoom" :options="options" :center="center">
            <!-- 載入地圖資料 -->
            <l-tile-layer :url="url" :attribution="attribution" />
            <!-- 地標 -->
            <l-marker
              ref="location"
              :lat-lng="item.local"
              v-for="item in stationlatlng"
              :key="item.name"
            >
              <!-- 地標圖示 -->
              <l-icon :icon-url="iconimg" />
              <l-tooltip :options="tooltipOptions">{{ item.name }}</l-tooltip>
              <!-- 點擊圖標會彈出顯示的文字 -->
              <l-popup>{{ item.address }}</l-popup>
            </l-marker>
          </l-map>
        </el-card>
      </div>
    </div>
    <!-- 高雄主要河川目前最新污染資料 -->
    <hr class="hr" />
    <div class="childtitle four">高雄主要河川歷史污染資料</div>
    <div class="content">
      <div>
        <span class="historyWord"> 下方為高雄主要河川的歷史污染資料 </span>
        <span class="historyWord2">
          <br /><b>Tip：</b>點擊左方河川即可查看該河川的汙染程度歷史資訊
        </span>
      </div>
      <div
        class="content"
        v-loading="loading3"
        element-loading-text="等待政府資料中..."
      >
        <el-row>
          <el-tabs
            class="searchMainRiver"
            tab-position="top"
            type="border-card"
            stretch
            v-model="searchMainRiver"
            @tab-click="searchPollution"
          >
            <el-tab-pane
              v-for="item in NewRiverChart.yAxis.data"
              :key="item"
              :label="item"
              :name="item"
            >
            </el-tab-pane>
          </el-tabs>
          <el-card class="box-card">
            <el-row class="historyRow" :gutter="30">
              <el-col :xs="24" :span="12">
                <div style="margin-bottom: 10px; font-weight: bold">
                  歷史資料時間 : {{ searchRiverChart.xAxis.data[0] }} ~
                  {{
                    searchRiverChart.xAxis.data[
                      searchRiverChart.xAxis.data.length - 1
                    ]
                  }}
                </div>
                <!-- 顏色對照汙染程度 -->
                <el-row v-if="RPISearchTable.length" class="historyTableRow">
                  <el-col :xs="8" :span="4" style="text-align: right">
                    <span>未(稍)受汙染</span>
                  </el-col>
                  <el-col :xs="4" :span="2" class="colorBlock">
                    <div class="block1"></div>
                  </el-col>
                  <el-col :xs="8" :span="4" style="text-align: right">
                    <span>輕度汙染</span>
                  </el-col>
                  <el-col :xs="4" :span="2" class="colorBlock">
                    <div class="block2"></div>
                  </el-col>
                  <el-col :xs="8" :span="4" style="text-align: right">
                    <span>中度汙染</span>
                  </el-col>
                  <el-col :xs="4" :span="2" class="colorBlock">
                    <div class="block3"></div>
                  </el-col>
                  <el-col :xs="8" :span="4" style="text-align: right">
                    <span>嚴重汙染</span>
                  </el-col>
                  <el-col :xs="4" :span="2" class="colorBlock">
                    <div class="block4"></div>
                  </el-col>
                </el-row>
                <el-table
                  :data="RPISearchTable"
                  height="485"
                  :header-cell-style="{
                    background: '#5f5f5f',
                    color: 'white',
                    fontSize: '18px',
                  }"
                  :row-style="tablerowColor"
                  style="width: 100%"
                >
                  <el-table-column prop="year" label="年份"> </el-table-column>
                  <el-table-column prop="avgText" label="汙染程度">
                  </el-table-column>
                </el-table>
              </el-col>
              <el-col :xs="24" :span="12"
                ><chart
                  :options="searchRiverChart"
                  height="400"
                  style="width: 100%; height: 400px"
                ></chart
              ></el-col>
            </el-row>
          </el-card>
        </el-row>
      </div>
    </div>
    <div class="footer"></div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  async mounted() {
    this.getAllRiver();
    this.getNewestRiverData("二仁溪");
    this.getNewestRiverData("高屏溪");
    this.getNewestRiverData("阿公店溪");
    this.getNewestRiverData("鳳山溪");
    this.getStationdata([
      { from: "Environmental Protection Agency", riverName: "阿公店溪" },
    ]);
    this.getRPIdata("阿公店溪");
  },
  methods: {
    // 水庫汙染指數介紹table的row顏色設定
    tablerowColor({ row }) {
      if (row.avgText === "未 ( 稍 ) 受汙染") {
        return {
          "background-color": "#CCFF99",
        };
      }
      if (row.avgText === "輕度汙染") {
        return {
          "background-color": "#bbffee",
        };
      }
      if (row.avgText === "中度汙染") {
        return {
          "background-color": "#FFFACD",
        };
      }
      if (row.avgText === "嚴重汙染") {
        return {
          "background-color": "#FFCCCC",
        };
      }
    },
    // 河川汙染指數介紹table的header顏色設定
    tableHeaderColor({ columnIndex }) {
      if (columnIndex === 0) {
        return "background-color: #DDAA00;color: #fff;font-weight: 700;";
      }
      if (columnIndex === 1) {
        return "background-color: #91c7ae;color: #fff;font-weight: 700;";
      }
      if (columnIndex === 2) {
        return "background-color: #61a0a8;color: #fff;font-weight: 700;";
      }
      if (columnIndex === 3) {
        return "background-color: #d48265;color: #fff;font-weight: 700;";
      }
      if (columnIndex === 4) {
        return "background-color: #c23531;color: #fff;font-weight: 700;";
      }
    },
    // 選擇河川後 => 取得該河川歷史RPI資料
    searchPollution() {
      this.loading3 = true;
      this.getRPIdata(this.searchMainRiver);
    },
    // 選擇流域後 => 取得該流域所有監測站位置
    searchStation() {
      this.loading2 = true;
      let selectedItem = this.RiverArray.filter((data) => {
        return data.riverName === this.searchRiver;
      });
      this.getStationdata(selectedItem);
    },
    // 取得流域所有監測站位置
    getStationdata(selectedItem) {
      this.stationlatlng = [];
      console.log(selectedItem[0].riverName);
      console.log(selectedItem[0].from);
      axios
        .post(`${this.allURL}environmentAPI/stationName`, {
          river: selectedItem[0].riverName,
          from: selectedItem[0].from,
        })
        .then((res) => {
          console.log(res.data);
          res.data.siteData.forEach((item) => {
            let mapdetail = {
              name: "",
              address: "",
              local: [],
            };
            this.searchStationArray.push(item.SiteName);
            mapdetail.name = item.SiteName;
            mapdetail.address = "地址 : " + item.SiteAddress;
            mapdetail.local[0] = item.latitude;
            mapdetail.local[1] = item.longitude;
            this.stationlatlng.push(mapdetail);
          });
          this.center = this.stationlatlng[0].local;
          console.log(this.stationlatlng);
          this.loading2 = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 取得主要河川汙染程度資料
    getRPIdata(riverName) {
      this.searchRiverChart.xAxis.data = [];
      this.searchRiverChart.series[0].data = [];
      axios
        .post(`${this.allURL}environmentAPI/RPIdata`, {
          river: riverName,
        })
        .then(async (res) => {
          console.log(res.data);
          this.RPISearchTable = res.data.pollution;
          res.data.pollution.forEach((item) => {
            this.searchRiverChart.xAxis.data.push(item.year);
            this.searchRiverChart.series[0].data.push(item.avg);
          });
          this.loading3 = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 取得最新重要河川污染資料
    getNewestRiverData(river) {
      axios
        .post(`${this.allURL}environmentAPI/newestRiverData`, {
          year: this.nowYear,
          river: river,
        })
        .then((res) => {
          console.log(res.data);
          this.nowYear = res.data.data.item1;
          this.NewRiverChart.yAxis.data.push(res.data.data.item2);
          this.NewRiverChart.series[0].data.push(res.data.data.value2);
          this.NewRiverChart.series[1].data.push(res.data.data.value3);
          this.NewRiverChart.series[2].data.push(res.data.data.value4);
          this.NewRiverChart.series[3].data.push(res.data.data.value5);
          if (this.NewRiverChart.series[0].data.length === 4) {
            this.loading = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 取得兩單位所有高雄的環境水體流域名稱
    getAllRiver() {
      axios
        .get(`${this.allURL}environmentAPI/riverName`)
        .then((res) => {
          console.log(res.data);
          this.RiverArray = res.data.river;
          console.log(this.RiverArray);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  data() {
    return {
      // 主要河川汙染資訊圖表
      searchRiverChart: {
        title: {
          text: "RPI指數變化圖",
          textStyle: {
            color: "#006030",
            fontSize: 16,
            align: "center",
          },
          itemGap: 7,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        xAxis: {
          type: "category",
          data: [],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: [],
            name: "RPI指數",
            type: "line",
          },
        ],
      },
      // 高雄主要河川目前最新污染表格資料
      NewRiverChart: {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        legend: {
          data: [
            "嚴重污染長度",
            "中度污染長度",
            "輕度污染長度",
            "未(稍)受污染長度",
          ],
        },
        grid: {
          left: "1%",
          right: "2%",
          bottom: "1%",
          containLabel: true,
        },
        xAxis: {
          type: "value",
        },
        yAxis: {
          type: "category",
          data: [],
        },
        series: [
          {
            name: "未(稍)受污染長度",
            type: "bar",
            stack: "总量",
            color: "#91c7ae",
            data: [],
          },
          {
            name: "輕度污染長度",
            type: "bar",
            stack: "总量",
            color: "#61a0a8",
            data: [],
          },
          {
            name: "中度污染長度",
            type: "bar",
            stack: "总量",
            color: "#d48265",
            data: [],
          },
          {
            name: "嚴重污染長度",
            type: "bar",
            stack: "总量",
            color: "#c23531",
            data: [],
          },
        ],
      },
      // 河川污染指數表格介紹資料
      RPIExplanTable: [
        {
          item: "溶氧量(DO)mg/L",
          nonePollution: "DO ≧ 6.5",
          littlePollution: "6.5 ＞ DO ≧ 4.6",
          miiddlePollution: "4.5 ≧ DO ≧ 2.0",
          hardPollution: "DO ＜ 2.0",
        },
        {
          item: "生化需氧量(BOD5)mg/L",
          nonePollution: "BOD5 ≦ 3.0",
          littlePollution: "3.0 ＜ BOD5 ≦ 4.9",
          miiddlePollution: "5.0 ≦ BOD5 ≦ 15.0",
          hardPollution: "BOD5 ＞ 15.0",
        },
        {
          item: "氨氮(NH3-N)mg/L",
          nonePollution: "NH3-N ≦ 0.50",
          littlePollution: "0.50 ＜ NH3-N ≦ 0.99",
          miiddlePollution: "1.00 ≦ NH3-N ≦ 3.00",
          hardPollution: "NH3-N ＞ 3.00",
        },
        {
          item: "懸浮固體(SS) mg/L",
          nonePollution: "SS ≦ 20.0",
          littlePollution: "20.0 ＜ SS ≦ 49.9",
          miiddlePollution: "50.0 ≦ SS ≦ 100",
          hardPollution: "SS ＞ 100",
        },
        {
          item: "點數",
          nonePollution: "1",
          littlePollution: "3",
          miiddlePollution: "6",
          hardPollution: "10",
        },
      ],
      RPISearchTable: [], // 高雄主要河川歷史資訊
      stationlatlng: [], // 進階搜尋地圖的測站打點
      RiverArray: [], // 進階搜尋的"流域"下拉選項
      searchStationArray: [], // 進階搜尋的"測站"下拉選項
      searchYearArray: [], // 進階搜尋的"年份"下拉選項
      searchRiver: "阿公店溪", // 要查看監測站的流域名稱
      searchMainRiver: "阿公店溪", // 要查看RPI歷史資料的河川名稱
      loading: true, // 目前最新污染資料loading
      loading2: true, // 環境水體監測站位置loading
      loading3: true, // 高雄主要河川歷史污染資料loading
      nowYear: 0, // 有高雄主要河川污染表格資料的最新年分
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 11,
      //地圖中心
      center: [22.612786, 120.442959],
      // 地標圖示
      iconimg: require("leaflet/dist/images/marker-icon.png"),
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      //tooltip彈框設定
      tooltipOptions: {
        permanent: true,
        offset: [10, 10],
      },
    };
  },
};
</script>
<style lang="scss" scoped>
.environment {
  overflow: hidden;
  padding: 2rem 4rem;
  background-color: #e3f0f7;
  font-family: 微軟正黑體;
  font-size: 22px;
  .footer {
    background-color: #e3f0f7;
    height: 100px;
  }
  .title {
    margin: 0 auto 10px auto;
    border-color: #dcdcdc;
    border-width: 2px;
    padding: 1%;
    display: inline-block;
    line-height: 60px;
    letter-spacing: 10px;
    text-decoration: none;
    font-weight: 600;
    outline: 0;
    font-size: 2.5em;
  }
  .content {
    padding: 20px 50px;
    margin: 0 auto;
  }
  .subtitle {
    margin-top: 10px;
    font-size: 22px;
    letter-spacing: 3px;
  }
  .highlight {
    font-weight: bold;
    font-size: 24px;
    color: #ffaa33;
  }
  .childtitle {
    font-size: 1.8em;
    font-weight: 600;
    color: #fff;
    letter-spacing: 10px;
    border-radius: 10px;
    padding: 5px;
    background-color: #064780;
    text-align: center;
    margin: 0 0 20px 0;
  }
  .childtitle.one {
    width: 300px;
  }
  .childtitle.two {
    width: 730px;
  }
  .childtitle.three {
    width: 470px;
  }
  .childtitle.four {
    width: 620px;
  }
  .historyRow {
    display: block;
  }
  .historyTableRow {
    display: block;
  }
  .searchMainRiver {
    height: auto;
  }
  .newestRow {
    display: flex;
    align-items: flex-end;
    .newestTime {
      text-align: right;
    }
